// Background Colors

.bg-danger {
  background-color: #FF6177 !important;

  &-light {
    background-color: transparentize(#FBD1D7, 0.5) !important;
  }
}

.bg-success {
  background-color: #03CEA4 !important;

  &-light {
    background-color: transparentize(#DAEEE9, 0.5) !important;
  }
}

.bg-warning {
  background-color: #FFBA52 !important;

  &-light {
    background-color: transparentize(#FFF8ED, 0.5) !important;
  }
}

// Text Colors

.text-danger {
  color: #FF6177 !important;

  &-light {
    color: transparentize(#FBD1D7, 0.5) !important;
  }
}

.text-success {
  color: #03CEA4 !important;

  &-light {
    color: transparentize(#DAEEE9, 0.5) !important;
  }
}

.text-warning {
  color: #FFBA52 !important;

  &-light {
    color: transparentize(#FFF8ED, 0.5) !important;
  }
}
