@use "sass:map";
@import 'variable';

.btn {
  // Base Button
  &.btn-primary {
    background-color: map.get($theme-colors, "primary");
    color: map.get($theme-colors, "light");
  }

  &.btn-success {
    background-color: map.get($theme-colors, "success");
    color: map.get($theme-colors, "light");
  }

  &.btn-danger {
    background-color: map.get($theme-colors, "danger");
    color: map.get($theme-colors, "light");
  }

  &.btn-warning {
    background-color: map.get($theme-colors, "warning");
    color: map.get($theme-colors, "light");
  }

  // Button Light
  &.btn-light-primary {
    background-color: map.get($theme-light-colors, "primary");
    color: map.get($theme-colors, "dark");
  }

  &.btn-light-success {
    background-color: map.get($theme-light-colors, "success");
    color: map.get($theme-colors, "dark");
  }

  &.btn-light-danger {
    background-color: map.get($theme-light-colors, "danger");
    color: map.get($theme-colors, "dark");
  }

  &.btn-light-warning {
    background-color: map.get($theme-light-colors, "warning");
    color: map.get($theme-colors, "dark");
  }

  // Button Outline
  &.btn-outline-primary {
    background-color: map.get($theme-light-colors, "primary");
    border-color: map.get($theme-colors, "primary");
    color: map.get($theme-colors, "dark");
  }

  &.btn-outline-success {
    background-color: map.get($theme-light-colors, "success");
    border-color: map.get($theme-colors, "success");
    color: map.get($theme-colors, "dark");
  }

  &.btn-outline-danger {
    background-color: map.get($theme-light-colors, "danger");
    border-color: map.get($theme-colors, "danger");
    color: map.get($theme-colors, "dark");
  }

  &.btn-outline-warning {
    background-color: map.get($theme-light-colors, "warning");
    border-color: map.get($theme-colors, "warning");
    color: map.get($theme-colors, "dark");
  }
}
