@use "sass:map";
@import "variable";

.workspace-card-box {
  padding: 0 30px;
  margin-bottom: 25px;
}

.workshop-card {
  overflow: hidden;
  padding: 0;
  font-family: map.get($font-family, "montserrat"), sans-serif;
  font-weight: map.get($font-weights, "medium");

  .card-header {
    height: 190px;
    width: 100%;

    .image {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }

    .info-tag {
      position: absolute;
      font-family: inherit;
      font-weight: inherit;
      font-size: 10px;
      top: 0;
      right: 0;
      transform: translate(-10px, 10px);
      padding: 6px 12px;
      background-color: rgba(255, 255, 255, 0.8);
      color: map.get($theme-colors, "dark");
      border-radius: 15px;
    }
  }

  .card-body {
    padding: 10px 20px 20px;
    color: map.get($theme-colors, "dark");

    .title {
      font-family: inherit;
      font-weight: inherit;
      font-size: 18px;
    }

    .author {
      font-family: inherit;
      font-weight: inherit;
      font-size: 14px;
      margin-top: 10px;

      &:before {
        content: "- By ";
      }
    }

    .workshop-info-box {
      display: inline-flex;
      justify-content: flex-start;
      align-items: flex-start;
      width: 100%;
      margin-top: 10px;
      flex-wrap: wrap;
      row-gap: 7px;

      .workshop-info {
        font-family: inherit;
        font-weight: map.get($font-weights, "regular");
        font-size: 12px;
        width: 50%;
        display: inline-flex;
        justify-content: flex-start;
        align-items: center;
        gap: 5px;
        color: map.get($theme-colors, "gray");

        .icon {

        }

        .text {

        }

        &:last-child {
          &:nth-child(2n-1) {
            width: 100%;
          }
        }
      }
    }

    .card-action {
      margin-top: 15px;
      display: inline-flex;
      justify-content: flex-start;
      align-items: flex-start;
      height: 40px;
      width: 100%;
      gap: 20px;

      .detail-button {
        font-family: inherit;
        font-weight: inherit;
        font-size: 12px;
        flex-grow: 1;
        height: 100%;
        padding: 0;
        display: inline-flex;
        justify-content: center;
        align-items: center;

        .icon {
          color: map.get($theme-colors, "success");
        }
      }

      .edit-button {
        font-family: inherit;
        font-weight: inherit;
        font-size: 12px;
        flex-shrink: 0;
        height: 100%;
        width: 90px;
        padding: 0;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        //background-color: rgba(3, 206,164, 0.05);

      }
    }
  }
}
