.status-card {
  padding: 25px;
  height: 172px;

  .card-body {
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    height: 90px;
    width: 100%;

    .status-text {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: flex-start;
      flex-shrink: 0;

      .counter {
        font-family: 'SF Pro Display', sans-serif;
        font-weight: 600;
        font-size: 48px;
      }

      .title {
        font-family: 'Montserrat', sans-serif;
        font-weight: 500;
        font-size: 16px;
        color: #1c1c1c;
      }
    }

    .status-icon {
      height: 90px;
      width: 90px;
      flex-shrink: 0;
      border-radius: 50%;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      overflow: hidden;

      .icon {
        object-fit: contain;
        width: 44px;
        height: 44px;
      }
    }
  }
}
