.sidenav {
  width: 120px;
  height: 100vh;
  background-color: #0063B2;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  flex-shrink: 0;

  .logo {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    overflow: hidden;
    padding: 10px;
    flex-shrink: 0;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  .nav-items {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    margin-top: 20px;
    overflow-y: auto;
    width: 100%;
    gap: 10px;
    list-style: none;
    -ms-overflow-style: none; /* Internet Explorer 10+ */
    scrollbar-width: none; /* Firefox */

    &::-webkit-scrollbar {
      display: none; /* Safari and Chrome */
    }

    .nav-item {
      width: 100px;
      height: 100px;
      flex-shrink: 0;

      a {
        font-size: 14px;
        font-family: 'SF Pro Display', sans-serif;
        font-weight: 300;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: #ffffff;
        border-radius: 5px;
        text-decoration: none;
        gap: 10px;

        .icon {
          width: 36px;
          height: 36px;
          object-fit: contain;
          flex-shrink: 0;
        }

        .title {
          text-align: center;
          width: 100%;
        }

        &.active {
          background-color: #005090;
        }
      }
    }
  }
}
