.custom-date-picker {
  padding: 0;
  height: 45px;
  width: 187px;
  border-radius: 8px;
  border: solid 1px #cccccc;
  background-color: #ffffff;

  .react-date-picker__wrapper {
    border: none;

    .react-date-picker__inputGroup {
      display: inline-flex;
      justify-content: center;
      align-items: center;
    }

    .react-date-picker__calendar-button {
      display: inline-flex;
      justify-content: center;
      align-items: center;
    }
  }
}
